	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Anthony Bull believes that all MVC frameworks make trade-offs, but that Ember.js provides the best balance for developing well-factored and ambitious web applications. This talk at the SF HTML5 User Group meetup on January 31st, 2013, features:\n \n-an introduction to ember.js\n-comparisons to other frameworks\n-code to demonstrate the ember.js syntax.\n \nFind out why Anthony calls ember.js the \"swiss-army-knife\" of developing frameworks.\n\n**Interested in a closer look at Anthony's slides? Check them out here: http://mrkn.co/enaa6","meta":{"duration":"PT28M39S"},"presenter":{"name":null},"source":{"id":"9a0OP4in-ac","provider":"youtube"},"slug":"snappy-performance-apps-with-emberjs","tag":["HTML5","SFHTML5","Anthony Bull","ember.js","applications","crowdflower","TechTv","performance"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/9a0OP4in-ac/default.jpg","height":90,"width":120}},"title":"Snappy Performance Apps with Ember.js"}},{"node":{"description":"New web developers have trouble distinguishing jQuery from JavaScript. We frequently point developers to three.js for doing 3D on the web, but what is raw WebGL and what tools do we have for debugging 3D web applications?","meta":{"duration":"PT55M21S"},"presenter":{"name":"Nick Desaulniers"},"source":{"id":"H4c8t6myAWU","provider":"youtube"},"slug":"mozilla-raw-webgl","tag":["HTML5","HTML5DevConf 2014","JavaScript","Node.js","Web Browser"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/H4c8t6myAWU/default.jpg","height":90,"width":120}},"title":"Mozilla Raw WebGL"}},{"node":{"description":"In my technical presentation (\"The Evolution of Java: Past, Present, and Future\"), I'll be discussing all of the changes to the Java programming language since its inception. In this this keynote, I'll focus my attention on the starting point: I'll present my candidates for the best and worst features in the platform as it was originally released (JDK 1.0), and explain the reasoning behind my choices. Some of my choices may be obvious, but others will likely surprise you. A splendid time is guaranteed for all.\n\nJosh Bloch\n\nGoogle\n\nJoshua Bloch is a software architect in the Open Source Program Office at Google, author of the bestselling, Jolt Award-winning \"Effective Java\" (Addison-Wesley, 2001; Second Edition, 2008), and coauthor of \"Java Puzzlers: Traps, Pitfalls, and Corner Cases\" (Addison-Wesley, 2005) and \"Java Conurrency in Practice\" (Addison-Wesley, 2006). He was previously a Distinguished Engineer at Sun Microsystems, where he led the design and implementation of numerous Java platform features including the Java Collections Framework and JDK 5.0 language enhancements. He holds a Ph.D. from CMU and a B.S. from Columbia.","meta":{"duration":"PT14M34S"},"presenter":{"name":null},"source":{"id":"hcY8cYfAEwU","provider":"youtube"},"slug":"oscon-java-2011-josh-bloch-java-the-good-bad-and-ugly-parts","tag":["oreilly","oreillymedia","oscon","opensource","java","data","perl","HTML5","android","mobile","conference","convention","O'Reilly","Tim O'Reilly","O'Reilly Media","OReilly","Tim OReilly","OReilly Media","OReillyMedia","o'reilly","books"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/hcY8cYfAEwU/default.jpg","height":90,"width":120}},"title":"OSCON Java 2011:  Josh Bloch, \"Java: The Good, Bad, and Ugly Parts\""}},{"node":{"description":"HTML5 Boilerplate is a \"rock-solid defualt for HTML5 awesome.\" In this video, Paul Irish, the man behind the project will show you how you can use HTML5 Boilerplate to get your projects up and running quickly while keeping best practices covered.\n\nPaul will start with an overview of HTML5 Boilerplate before diving into some examples and demos to give you a feel for how you can jumpstart your next project with HTML5 Boilerplate.\n\n** Head over to http://marakana.com to learn more about HTML5 and open source development **","meta":{"duration":"PT1H4M20S"},"presenter":{"name":null},"source":{"id":"qyM37XKkmKQ","provider":"youtube"},"slug":"paul-irish-on-html5-boilerplate","tag":["HTML5","Development","Paul","Irish","Boilerplate","open","source","tutorial","Marakana","example","techtv","San","Francsico","User","Group"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/qyM37XKkmKQ/default.jpg","height":90,"width":120}},"title":"Paul Irish on HTML5 Boilerplate"}}]}},"pathContext":{"slug":"html5","tag":"HTML5"}}

